import React, { useEffect, useState } from 'react'
import PropTypes from "prop-types"

import avatar from '../../images/me.png'

import {
  Container,
  CustomHeader,
  HeaderAvatar,
  HeaderAvatarImage,
  HeaderAvatarName,
  HeaderLink,
  HeaderNavigation,
  HeaderContainer
} from "./styles"

function Header({ siteTitle }) {

  const [ scrollPos, setScrollPos ] = useState(0)
  const [ deviceHeight, setDeviceHeight ] = useState(0)
  const [ section, setSection] = useState(1)

  const handleScroll = () => {
    setDeviceHeight(window.innerHeight)
    setScrollPos(document.body.getBoundingClientRect().top * -1)
    if (scrollPos === 0) {
      setSection(1)
    } else {
      setSection(Math.floor(((scrollPos) / deviceHeight) + 1))
    }
  }

  const scrollToElement = (id) => {
    handleScroll()
    const el = document.getElementById(id)
    setDeviceHeight(window.innerHeight)
    setScrollPos(el.offsetTop)
    setSection(Math.floor(((scrollPos) / deviceHeight) + 1))
  }

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
  })

  return (
    <CustomHeader section={section}>
      <Container>
        <HeaderContainer>
          <HeaderAvatar>
            <HeaderAvatarImage src={avatar} />
            <HeaderAvatarName>{siteTitle}</HeaderAvatarName>
          </HeaderAvatar>
          <HeaderNavigation>
            <HeaderLink 
              onClick={() => scrollToElement('home')}
              href="#home"
            >Home</HeaderLink>
            <HeaderLink
              onClick={() => scrollToElement('carrer')}
              href="#carrer"
            >Carreira</HeaderLink>
            <HeaderLink
              onClick={() => scrollToElement('skill')}
              href="#skill"
            >Habilidades</HeaderLink>
            <HeaderLink
              onClick={() => scrollToElement('education')}
              href="#education"
            >Graduao</HeaderLink>
            <HeaderLink
              onClick={() => scrollToElement('certificate')}
              href="#certificate"
            >Certificados</HeaderLink>
          </HeaderNavigation>
        </HeaderContainer>
      </Container>
    </CustomHeader>
  )
} 

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header